<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'countries',

  computed: {
    ...mapState('summaryStatus', {
      loading: 'updating'
    }),

    ...mapGetters('summary', {
      totalConfirmed: 'getTotalConfirmed', 
      totalDeaths: 'getTotalDeaths', 
      totalRecovered: 'getTotalRecovered'
    }),
  },

  filters: {
    toLocaleString (value) {
      return value.toLocaleString()
    }
  }
}
