  $theme-colors
);


// Card variables

$card-spacer-y:             1.5rem;
$card-spacer-x:             1.5rem;
$card-border-width:         1px;
$card-border-radius:        .8rem;
$card-border-color:         hsla(0, 0%, 0%, 0.125);
$card-cap-bg:               rgba(#000, .03);
