$card-height:               null;
$card-color:                null;
$card-bg:                   white;

$card-img-overlay-padding:  1.25rem;

$card-group-margin:         40px / 2;
$card-deck-margin:          $card-group-margin;

$card-columns-gap:          1.25rem;
$card-columns-margin:       $card-spacer-y;


$enable-rounded:            true;

// Card

//
// Base styles
//

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  height: $card-height;
  word-wrap: break-word;
  background-color: $card-bg;
  background-clip: border-box;
  border: $card-border-width solid $card-border-color;
  border-radius: $card-border-radius;

  > hr {
    margin-right: 0;
    margin-left: 0;
  }

  > .list-group {
    border-top: inherit;
    border-bottom: inherit;

    &:first-child {
      border-top-width: 0;
      border-top-right-radius: $card-border-radius;
      border-top-left-radius: $card-border-radius;
    }

    &:last-child  {
      border-bottom-width: 0;
      border-bottom-right-radius: $card-border-radius;
      border-bottom-left-radius: $card-border-radius;
    }
  }

  > .card-header + .list-group,
  > .list-group + .card-footer {
    border-top: 0;
  }
}

.card-body {
  flex: 1 1 auto;
  min-height: 1px;
  padding: $card-spacer-x;
  color: $card-color;
}

.card-title {
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 1.5;
  margin-bottom: .5rem;
}

.card-subtitle {
  font-weight: 400;
  margin-top: .2rem;
  margin-bottom: 0;
}

.card-text:last-child {
  margin-bottom: 0;
}

//
// Header navs
//

.card-header-tabs {
  border-bottom: 0;
}

// Card image
.card-img-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: $card-img-overlay-padding;
  border-radius: $card-border-radius;
}

.card-img,
.card-img-top,
.card-img-bottom {
  flex-shrink: 0; 
  width: 100%; 
}

.card-img,
.card-img-top {
  border-top-right-radius: $card-border-radius;
  border-top-left-radius: $card-border-radius;
}

.card-img,
.card-img-bottom {
  border-bottom-right-radius: $card-border-radius;
  border-bottom-left-radius: $card-border-radius;
}


// Card deck

.card-deck {
  .card {
    margin-bottom: $card-deck-margin;
  }

  @media (min-width: 576px) {
    display: flex;
    flex-flow: row wrap;
    margin-right: -$card-deck-margin;
    margin-left: -$card-deck-margin;

    .card {
      flex: 1 0 0%;
      margin-right: $card-deck-margin;
      margin-bottom: 0;
      margin-left: $card-deck-margin;
    }
  }
}


//
// Card groups
//

.card-group {
  > .card {
    margin-bottom: $card-group-margin;
  }

  @media (min-width: 576px) {
    display: flex;
    flex-flow: row wrap;
    > .card {
      flex: 1 0 0%;
      margin-bottom: 0;

      + .card {
        margin-left: 0;
        border-left: 0;
      }

      // Handle rounded corners
      @if $enable-rounded {
        &:not(:last-child) {
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;

          .card-img-top,
          .card-header {
            border-top-right-radius: 0;
          }
          .card-img-bottom,
          .card-footer {
            border-bottom-right-radius: 0;
          }
        }

        &:not(:first-child) {
          border-top-left-radius: 0;
          border-top-left-radius: 0;

          .card-img-top,
          .card-header {
            border-top-left-radius: 0;
          }
          .card-img-bottom,
          .card-footer {
            border-bottom-left-radius: 0;
          }
        }
      }
    }
  }
}


//
// Columns
//

.card-columns {
  .card {
    margin-bottom: $card-columns-margin;
  }

  @media (min-width: 576px) {
    column-count: 1;
    column-gap: $card-columns-gap;
    orphans: 1;
    widows: 1;

    .card {
      display: inline-block;
      width: 100%;
    }
  }
  @media (min-width: 768px) {
    column-count: 2;
    column-gap: $card-columns-gap;
    orphans: 1;
    widows: 1;
  }
  @media (min-width: 992px) {
    column-count: 2;
    column-gap: $card-columns-gap;
    orphans: 1;
    widows: 1;
  }
  @media (min-width: 1200px) {
    column-count: 3;
    column-gap: $card-columns-gap;
    orphans: 1;
    widows: 1;
  }
}

// Lis colors
// blue, indigo, purple, red, orange, yellow, green, teal, cyan, white, gray, gray-dark 

@each $overlay, $value in $colors{ // No need white and gray
  .card-img-overlay.--card-img-overlay-#{$overlay}{
    background-color: darken(rgba($value, .70), 30%);
  }
}

// Card radius

.card-radius{
  border-radius: $card-border-radius;
}

// card display content

.card-overflow-content{
  overflow: hidden;
  &:hover{
    .card-display-content{
      transform: translateY(0);
      box-shadow: 0px -10px 17px 3px #00000040;
    }  
  }
  .card-display-content{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateY(100%);
    background-color: white;
    padding: 2rem 1.5rem;
    transition: all .3s ease-in-out;
    border-radius: $card-border-radius;
    z-index: 2;
  }
}


// Footer

.footer{
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  a{ color: #000; }
  p{
    margin: 0;
    color: #576674;
  }
  .yo-content{
    margin-bottom: .3rem;
    font-weight: 500;
    color: #000;
  }
  .yo{
    text-decoration: underline;
  }
  .footer-social{
    display: flex;
    align-items: center;
  }

  .footer-social-link{
    width: 2.8rem;
    height: 2.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4rem;
    transition: .3s linear;
    &:hover{
      background-color: #bac1c9;
    }
  }
}

</style>