<template>
   <div id="app" class="container-fluid h-100">
    <nav class="navbar navbar-light bg-light navbar-expand-md">
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#mainNavbar" aria-controls="mainNavbar" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button> 
      <div class="collapse navbar-collapse" id="mainNavbar">
        <div class="navbar-nav">
            <router-link class="navbar-brand mb-0 h1 nav-item nav-link cursor-pointer" to="/">Home</router-link> 
            <router-link class="navbar-brand mb-0 h1 nav-item nav-link" to="/countries">Countries</router-link> 
            <router-link class="navbar-brand mb-0 h1 nav-item nav-link" to="/about">About</router-link> 
        </div>
        <div class="navbar-nav ml-auto">
          <a class="nav-item nav-link ml-auto mb-0 h5">{{ lastUpdated | isoTimeToReadable }}</a>
        </div>
      </div>
    </nav>
    <router-view/>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

import { isoTimeToReadable } from '@/utils/DateTime'

export default {
  name: 'App',
  data () {
    return {
      currentComponent: 'home'
    }
  },

  created () {
    this.init()
  },

  computed: {
    ...mapState('summaryStatus', {
      loading: 'updating'
    }),

    ...mapGetters('summary', {
      newConfirmed: 'getNewConfirmed', 
      totalConfirmed: 'getTotalConfirmed', 
      newDeaths: 'getNewDeaths',
      totalDeaths: 'getTotalDeaths', 
      newRecovered: 'getNewRecovered', 
      totalRecovered: 'getTotalRecovered'
    }),

    ...mapState('summary', {
      lastUpdated: 'lastRevision'
    }),
  },

  methods: {
    init () {
      this.updateCountries()
    },

    updateCountries () {
      this.$store.dispatch('summary/fetchSummary')
    },

    selectComponent (componentName) {
      console.debug(`selecting component ${componentName}`)
      this.currentComponent = componentName
    }
  },

  filters: {
    isoTimeToReadable (value) {
      return isoTimeToReadable(value)
    }
  }
}
</script>

<style>
html, body {
    height: 100%;
}
.cursor-pointer {cursor: pointer;}
</style>
